#modules {
  width: 224px;
  border-right: 1px solid var(--divider-color);
  padding-right: 16px;

  .platforms {
    display: flex;
    padding: 16px;
    margin: -16px -16px 8px;
    border-bottom: 1px solid var(--divider-color);

    .label {
      flex: 1;
      text-transform: capitalize;
      margin-left: 16px;
    }

    &:hover {
      cursor: pointer;
      background-color: var(--hover-bg-color);
    }
  }

  .module {
    padding: 8px 16px;
    margin: 0px -16px;
    text-transform: capitalize;

    &:hover {
      cursor: pointer;
      background-color: var(--hover-bg-color);
    }

    &.is-selected {
      color: var(--secondary);
      font-weight: 500;
    }
  }

  .add-module {
    border-top: 1px solid var(--divider-color);
    padding-top: 16px;
    margin-top: 8px;
  }
}
</style>
